atlas_3 : [
{src : atlas_7},
{src : atlas_8},
{src : atlas_9},
{src : atlas_10},
{src : atlas_11},
{src : atlas_12}
],
showTab1 : true,
showTab2 : false,
showTab3 : false
}
},
methods : {
showTab (ev){
if(ev.target.title == '图集三'){
this.showTab1 = true,
this.showTab2 = false,
this.showTab3 = false
}else if(ev.target.title == '图集二'){
this.showTab2 = true,
this.showTab1 = false,
this.showTab3 = false
}else{
this.showTab3 = true,
this.showTab2 = false,
this.showTab1 = false
}
},
shake (ev){
ev.target.classList.add('shake');
setTimeout(function (){
ev.target.classList.remove('shake');
},950)
},
zoom (ev){

ev.target.classList.add('active');

setTimeout(function (){
ev.target.classList.remove('active');
},600);

}
}
}



